.jun-alert-div {
  position: relative;
  display: block;
  height: auto;
  padding: 10px 20px;
  text-align: left;
  border: none; }
  .jun-alert-div .jun-alert-close {
    width: 16px;
    height: 16px;
    display: inline-block;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 10px;
    margin: auto; }
    .jun-alert-div .jun-alert-close:hover .jun-icon {
      transform: scale(1.3); }

.jun-alert-div.is-border {
  border-width: 1px;
  border-style: solid; }

