  border-color: #acdbf3; }

.jun-alert-div.jun-alert-type-success {
  color: #009900;
  background-color: #f2ffea;
  border-color: #c7ddb9; }

.jun-alert-div.jun-alert-type-info {
  color: #555555;
  background-color: #f9f9f9;
  border-color: #dddddd; }

.jun-alert-div.jun-alert-type-warning {
  color: #f68300;
  background-color: #fcf8e2;
  border-color: #edd28a; }

.jun-alert-div.jun-alert-type-danger {
  color: #ee2117;
  background-color: #fff6f2;
  border-color: #f1acac; }

.jun-alert-div.is-left-border {
